import { ToastrService } from 'ngx-toastr';
import { Injectable } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SubscribeAdd, SubscribeRe } from 'src/app/demo/_model/subscribe';
import { SubscribeType } from 'src/app/demo/_model/subscribeType';
import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
@Injectable({
  providedIn: 'root'
})
export class MshpMembershipTypeSharedService {
  AddForm: FormGroup;
  SubscribeTypes: SubscribeType[];
  SubscribeTypeService: BaseService<SubscribeType>;
  constructor(private formBuilder: FormBuilder,  private httpSv: HttpService,private toastr:ToastrService) {
    this.SubscribeTypeService = new BaseService<SubscribeType>(httpSv, ApiPath.SubscribeType);
   }
   initForm(data:SubscribeType = null) {
    this.AddForm = this.formBuilder.group({
      id:[data?.id | 0],
      name: [data?.name, Validators.required],
      arabPrice: [data?.arabPrice, Validators.required],
      forignPrice: [data?.forignPrice, Validators.required],

    });
}
Add(modalMember:any): void {
  let data: SubscribeType = this.AddForm.value;
console.log(data);


  this.SubscribeTypeService.Add(data).subscribe((res) => {
    this.toastr.success("تمت الاضافة بنجاح")
    modalMember.hide();
  });
}


Edit(subscribe: SubscribeType,modal:any){
this.initForm(subscribe);
modal.show();
}
editSubscribe(modaleditMember:any){
  let data: SubscribeType = this.AddForm.value;

  this.SubscribeTypeService.Update(data).subscribe((res) => {
    this.toastr.success("تم التعديل بنجاح")
    modaleditMember.hide();
      console.log(res);
  });
}
confirmAlert(id :number) {
  Swal.fire({
      title: 'هل انت متاكد من حذف نوع الباقة',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
  }).then((willDelete) => {
      if (willDelete.dismiss) {
          Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
    this.SubscribeTypeService.Delete(id).subscribe(res=>{
       let index = this.SubscribeTypes.findIndex((x) => x.id == id);
        this.SubscribeTypes.splice(index, 1);
          Swal.fire('', 'تم حذف نوع الباقة', 'success');
    });
   }
  });
}

}
