
import { MshpMembershipTypeSharedService } from './mshp-membershipType-shared.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SubscribeRe, SubscribeAdd } from 'src/app/demo/_model/subscribe';
import { SubscribeType } from 'src/app/demo/_model/subscribeType';

@Component({
    selector: 'app-mshp-membershipType',
    templateUrl: './mshp-membershipType.component.html',
    styleUrls: ['./mshp-membershipType.component.scss']
})
export class MshpMembershipTypeComponent implements OnInit {
  AddForm: FormGroup;
  SubscribeTypes: SubscribeType[];
  Subscribes: SubscribeRe[];
  SubscribeTypeService: BaseService<SubscribeType>;
  SubscribeService: BaseService<SubscribeAdd>;
    constructor(private formBuilder: FormBuilder,  private httpSv: HttpService, public shared: MshpMembershipTypeSharedService) {
      this.SubscribeTypeService = new BaseService<SubscribeType>(httpSv, ApiPath.SubscribeType);
      this.SubscribeService = new BaseService<SubscribeAdd>(httpSv, ApiPath.Subscribe);
    }

    ngOnInit() {
      this.GetSubscribeTypes();
      this.GetSubscribes();
    }

    GetSubscribeTypes(){
      this.SubscribeTypeService.GetAll().subscribe((res) => {
        const data = res.map((x) => {
          const temp = {
              label: x.name,
              value: x.id.toString()
          };
          return temp;
      });
      this.shared.SubscribeTypes = data;
    });
    }

    GetSubscribes(){
      this.SubscribeTypeService.GetAll().subscribe((res) => {
        this.shared.SubscribeTypes = res;
        console.log(res);

    });
  }








}
